import { call, put, takeLatest } from 'redux-saga/effects';
import { OFFER } from './Constants';
import {
  updateOfferStepSuccess,
  updateOfferStepFailure,
  getAllStepsSuccess,
  getAllStepsFailure,
} from './Actions';
import { OfferAPI } from '../services/OfferApi';

const offerApi = new OfferAPI();

export function* getAllSteps() {
  yield takeLatest(OFFER.getAllSteps, getAllStepsFromAPI);
}

function* getAllStepsFromAPI({ type, payload }) {
  try {
    const { data } = yield call(OfferAPI.getAllSteps, { endpoint: payload });
    yield put(getAllStepsSuccess({ ...data }));
  } catch (error) {
    yield put(getAllStepsFailure(error));
  }
}

export function* updateOfferStep() {
  yield takeLatest(OFFER.updateOfferStep, updateOfferStepToAPI);
}

function* updateOfferStepToAPI({ type, payload }) {
  try {
    const { data } = yield call(offerApi.updateOfferStep, payload);
    yield put(updateOfferStepSuccess({ ...data }));
  } catch (e) {
    yield put(updateOfferStepFailure(e));
  }
}
