import { all } from 'redux-saga/effects';
import {
  getForm,
  saveFormData,
  saveFormFiles,
} from 'components/FormBuilder/redux/Sagas';
import { updateOfferStep, getAllSteps } from 'containers/Offer/redux/Sagas';
import { getForms } from 'components/SellerProperty/Listing/Form/redux/Sagas';
import { getOffers, saveOfferStep } from 'containers/Seller/Offer/redux/Sagas';

export default function* rootSaga() {
  yield all([
    getForm(),
    saveFormData(),
    saveFormFiles(),
    getForms(),
    getOffers(),
    saveOfferStep(),
    updateOfferStep(),
    getAllSteps(),
  ]);
}
