import { OFFER } from './Constants';

export const updateOfferStep = data => {
  return {
    type: OFFER.updateOfferStep,
    payload: data,
  };
};

export const updateOfferStepSuccess = data => {
  return {
    type: OFFER.updateOfferStepSuccess,
    payload: data,
  };
};

export const updateOfferStepFailure = data => {
  return {
    type: OFFER.updateOfferStepFailure,
    payload: data,
  };
};

export const getAllSteps = payload => ({
  type: OFFER.getAllSteps,
  payload,
});

export const getAllStepsSuccess = payload => ({
  type: OFFER.getAllStepsSuccess,
  payload,
});

export const getAllStepsFailure = payload => ({
  type: OFFER.getAllStepsFailure,
  payload,
});
