import React from 'react';
import withCustomAuthenticator from './Authenticator/withCustomAuthenticator';
import qs from 'query-string';
import { Auth } from 'aws-amplify';
import axios from 'axios';
import * as Consts from '../shared/Consts';
import { toastr } from 'react-redux-toastr';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';
import moment from 'moment';
import _isEmpty from 'lodash/isEmpty';
import OfferBuyerList from 'containers/Offer/OfferBuyerList';

import ListPropertyForm from 'components/SellerProperty/Listing/Form/Loadable';
import OfferList from 'containers/Seller/Offer/OfferList/Loadable';

const TAB_NAME_OFFER = 'OFFER';
const TAB_NAME_SELL = 'SELL';
const STYLE_FULL_WIDTH = { width: '100%' };

class Dashboard extends React.Component {
  constructor(props) {
    super();
    this.switchTabHandler = this.switchTabHandler.bind(this);

    const queryString = qs.parse(props.location.search, {
      ignoreQueryPrefix: true,
    });
    var selectedTab =
      queryString.tab === undefined ? '' : queryString.tab.toUpperCase();
    if (selectedTab !== TAB_NAME_SELL) {
      selectedTab = TAB_NAME_OFFER;
    }
    this.state = {
      SelectedTab: selectedTab,
      Properties: undefined,
      Offers: undefined,
      Loading: false,
    };
  }

  async componentDidMount() {
    try {
      this.setState({ Loading: true });

      const requestConfig = await Consts.GetRequestConfig(Auth);
      const getOffersUrl = `${Consts.API_BUYER_OFFER}?include=PropertyData,InitOffer,OfferDetail`;
      const getPropertiesUrl = Consts.API_SELLER_PROPERTIES;
      const responses = await axios.all([
        axios.get(getPropertiesUrl, requestConfig),
        axios.get(getOffersUrl, requestConfig),
      ]);

      this.setState({
        Loading: false,
        Properties: responses[0].data.Properties,
        Offers: responses[1].data.Offers,
      });
    } catch (error) {
      this.setState({ Loading: false });
      toastr.error('Load properties', 'Please refresh page and try again');
    }
  }

  switchTabHandler(tabName) {
    this.setState({ SelectedTab: tabName });
  }

  render() {
    const selectedTab = this.state.SelectedTab;

    return (
      <BlockUi
        tag="div"
        blocking={this.state.Loading}
        loader={<Loader active type="ball-pulse" color="rgba(231,28,99,1)" />}
      >
        <div className="hiw_section hiw_section_front">
          <div className="hiw_blocks hiw_blocks_front">
            <div
              className={
                selectedTab === TAB_NAME_OFFER
                  ? 'hiw_block hiw_block_front hiw_active'
                  : 'hiw_block hiw_block_front'
              }
              onClick={this.switchTabHandler.bind(this, TAB_NAME_OFFER)}
            >
              <div className="backss" />
              <div className="hiw_inner_block animated_fastest">
                Buying a Home
              </div>
            </div>
            <div
              className={
                selectedTab === TAB_NAME_OFFER
                  ? 'hiw_panel2 mobile2'
                  : 'hiw_panel2 mobile2 hidden_panel'
              }
            >
              <div
                className={
                  selectedTab === TAB_NAME_OFFER
                    ? 'hiw animated_fastest'
                    : 'hiw animated_fastest hide_it'
                }
              >
                <div
                  className="hiw_holder"
                  style={{ ...STYLE_FULL_WIDTH, paddingBottom: '0' }}
                >
                  {GenerateOfferList(this.state.Offers)}
                </div>
              </div>
            </div>
            <div
              className={
                selectedTab === TAB_NAME_SELL
                  ? 'hiw_block hiw_block_front hiw_active'
                  : 'hiw_block hiw_block_front'
              }
              onClick={this.switchTabHandler.bind(this, TAB_NAME_SELL)}
            >
              <div className="backss" />
              <div className="hiw_inner_block animated_fastest">
                Selling a Home
              </div>
            </div>
            <div
              className={
                selectedTab === TAB_NAME_SELL
                  ? 'hiw_panel2 mobile2'
                  : 'hiw_panel2 mobile2 hidden_panel'
              }
            >
              <div
                className={
                  selectedTab === TAB_NAME_SELL
                    ? 'hiw animated_fastest'
                    : 'hiw animated_fastest hide_it'
                }
              >
                <div
                  className="hiw_holder"
                  style={{ ...STYLE_FULL_WIDTH, paddingBottom: '0' }}
                >
                  {GeneratePropertyList(this.state.Properties)}
                </div>
              </div>
            </div>
          </div>
          <div className="hiw_panel desktop2">
            <div
              className={selectedTab === TAB_NAME_OFFER ? 'hiw' : 'hiw hide_it'}
            >
              <div className="hiw_holder" style={STYLE_FULL_WIDTH}>
                {GenerateOfferList(this.state.Offers)}
              </div>
            </div>
            <div
              className={selectedTab === TAB_NAME_SELL ? 'hiw' : 'hiw hide_it'}
            >
              <div className="hiw_holder" style={STYLE_FULL_WIDTH}>
                {GeneratePropertyList(this.state.Properties)}
              </div>
              <div className="hiw_holder" style={STYLE_FULL_WIDTH}>
                <OfferList />
              </div>
            </div>
          </div>
        </div>
      </BlockUi>
    );
  }
}

export default withCustomAuthenticator(Dashboard);

const GenerateOfferList = offers => {
  if (offers === undefined) {
    // TBD
  } else {
    if (offers === null || offers.length === 0) {
      return <p className="incorrect_parameter">Offer Not Found</p>;
    }

    const orderedOffers = offers.sort((itemA, itemB) => {
      return moment(itemA.CreatedAt) - moment(itemB.CreatedAt);
    });
    return <OfferBuyerList offers={orderedOffers} />;
  }
};

const GeneratePropertyList = properties => {
  if (process.env.REACT_APP_SELL_SIDE_ENABLED !== '1') {
    return <p className="incorrect_parameter">Coming Soon</p>;
  }

  if (_isEmpty(properties)) {
    return <p className="incorrect_parameter">Property Not Found</p>;
  }

  const orderedProperties = properties.sort((itemA, itemB) => {
    return moment(itemA.CreatedAt) - moment(itemB.CreatedAt);
  });

  return (
    <>
      <div
        className="b_texts"
        style={{ textAlign: 'left', marginBottom: '5px' }}
      >
        The following modules can be filled out in any order; simply click one
        to begin. You can save your progress at any time
      </div>
      <ListPropertyForm properties={orderedProperties} />
    </>
  );
};
