import Request from 'helpers/Request';
import { DIGITAL_OFFER_BASE_ENDPOINT } from 'containers/Offer/Constants';

export class OfferAPI {
  updateOfferStep = ({ newStep, offerId }) => {
    const endpoint = `${DIGITAL_OFFER_BASE_ENDPOINT}/offers/${offerId}/buyersteps`;
    const body = { new_step: newStep };
    return Request.put(endpoint, body);
  };
}
